<!--入学咨询 -->
<template>
  <div class="enrolconsult">
    <!-- 标题栏 -->
    <div class="titleblock" style="height: 53px;">
      <span class="enroltitle">入学咨询</span>
      <button class="question" @click="dialogFormVisible = true">发布提问</button>
      <el-dialog title="我要提问" :visible.sync="dialogFormVisible"><ask @switch= 'dialogFormVisible = false' /></el-dialog>
    </div>
    <!-- 咨询列表 -->
    <div class="consultlist">
      <div class="consultcontent" :style="consultcontent">
        <div style="height:255px;background: #fff;position: relative;top: 8px;margin:0px 8px;border-radius:20px" @click="jump('/enrolconsultdetail',{questionID:'1'})">
          <img src="@/assets/img/enrolconsult/question.png">
          <img src="@/assets/img/enrolconsult/headimg.png" style="position: relative;top: 33px;">
          <div style="margin: -33px 53px auto 195px;height: 180px;line-height: 20px;color: rgba(16, 16, 16, 1);font-size: 14px;border: 1px solid rgba(187, 187, 187, 1);">
            <div class="text" style="margin:40px 270px auto 26px;height:82px;line-height: 41px;color: rgba(16, 16, 16, 1);font-size: 28px;text-align: left;">请问我女儿正上初二，数学比较差，有推荐的课程吗？</div>
            <div style="margin-right:37px;text-align: right;padding-right: 37px;height: 41px;line-height: 41px;color: rgba(16, 16, 16, 1);font-size: 28px;">2018-09-28</div>
          </div>
        </div>
      </div>
      <div class="consultcontent" :style="consultcontent">
        <div style="height:255px;background: #fff;position: relative;top: 8px;margin:0px 8px;border-radius:20px" @click="jump('/enrolconsultdetail',{questionID:'2'})">
          <img src="@/assets/img/enrolconsult/answer.png">
          <img src="@/assets/img/enrolconsult/headimg.png" style="position: relative;top: 33px;">
          <div style="margin: -33px 53px auto 195px;height: 180px;line-height: 20px;color: rgba(16, 16, 16, 1);font-size: 14px;border: 1px solid rgba(187, 187, 187, 1);">
            <div class="text" style="margin:40px 270px auto 26px;height:82px;line-height: 41px;color: rgba(16, 16, 16, 1);font-size: 28px;text-align: left;">不好意思，我儿子同学报名了您的这个课程，感觉不错，我也想报名参加，请问报名流程是什么？</div>
            <div style="margin-right:37px;text-align: right;padding-right: 37px;height: 41px;line-height: 41px;color: rgba(16, 16, 16, 1);font-size: 28px;">2018-09-27</div>
          </div>
        </div>
      </div>
    </div>
    <!-- 联系我们 -->
    <div class="contactus">
      <img src="@/assets/img/enrolconsult/contactus.png">
    </div>
  </div>
</template>
<script>
import Ask from '@/components/common/ask'

export default {
  name: 'enrolconsult-view',
  data() {
    return {
      dialogFormVisible: false,
    }
  },
  components: {
    ask: Ask,
  },
  created() {
    this.$emit('header', true);
    this.$emit('footer', false);
    this.consultcontent = `width:${window.innerWidth - 220}px;`;
  },
  methods: {
    jump(routename, params) {
      const routeData = this.$router.resolve({
        name: '',
        path: routename,
        query: params,
        params: {},
      });
      window.open(routeData.href, '_blank');
    },
  },
}
</script>
<style scoped>
  .enrolconsult{
    margin-left:34px;
  }
  .titleblock{
    margin-left: 10px;
    line-height: 44px;
    font-size: 30px;
  }
  .titleblock>>>.enroltitle{
    width: 121px;
    color: rgba(16, 16, 16, 1);
  }
  .titleblock>>>.question{
    margin-left:72px;
    width: 170px;
    height: 53px;
    border-radius: 18px;
    background-color: rgba(8, 150, 255, 1);
    color: rgba(255, 255, 255, 1);
    font-size: 30px;
  }
  .contactus{
    position: fixed;
    top: 35%;
    right: 37px;
  }
  .consultcontent{
    margin: 29px 0px 58px 0px;
    height: 275px;
    background-image: linear-gradient(to right , #F3F5F7, #F3F5F7);
    cursor:default;
  }
  .text{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  /* 此处有bug，在弹出框中使用ueditor，工具栏高度变大 */
  .titleblock>>>.edui-editor-toolbarboxouter .edui-default{
    height:32px !important;
  }
</style>



